@import "../variables";

.books .author {
  font-size: 110%;
  margin-top: -1.25rem;
  margin-bottom: 2rem;
}

.book-image,
.video-image,
.versions-image {
  line-height: 1;
  margin-right: 2rem;
  perspective: 50rem;
  transform-style: preserve-3d;
  position: relative;
  transition: 0.3s;
  border-radius: 0.2rem;
  img {
    position: relative;
    border-radius: 0.2rem;
    z-index: 1;
    width: 5rem;
    transition: 0.3s;
  }
  &.book-image-lg {
    position: absolute;
    left: -7rem;
    top: -0.25rem;
  }
}
.book-image::before {
  content: "";
  width: 1rem;
  height: 1rem;
  bottom: -0.5rem;
  left: 0;
  width: 100%;
  border-radius: 100%;
  filter: blur(0.4rem);
  transform: scale(1.25);
  position: absolute;
  transition: 0.3s;
  background-color: rgba(0, 0, 0, 0.25);
}
.blog-posts,
.blog-title {
  .l {
    flex: 0.6 0 0;
    > div {
      position: sticky;
      top: 1.5rem;
    }
  }
  .r {
    flex: 1 0 0;
  }
}
.blog-title {
  margin-bottom: 0;
  display: flex;
}
.blog-posts,
.pagination,
.intro > .blog-title {
  max-width: 960px;
  margin: auto;
}
.blog-posts article {
  display: flex;
  h2 {
    font-size: 120%;
    margin-bottom: 1.5rem;
    a {
      color: inherit;
    }
    + div {
      font-size: 90%;
    }
  }
}

.life .books-item,
.videos-item,
.versions-item {
  margin-top: 2rem;
  h2 {
    font-size: 125%;
    margin-bottom: 0.5rem;
    + div {
      color: $colorLight;
    }
  }
  a {
    display: block;
    color: inherit;
    text-decoration: none;
    &::after {
      display: none;
    }
    .content {
      display: flex;
      align-items: center;
      > div {
        flex: 1 0 0;
      }
    }
    &:focus {
      box-shadow: none;
      .book-image img {
        box-shadow: 0 0 0 0.2rem $focusRingColor;
        transform: scale(1.05);
      }
      .video-image,
      .versions-image {
        transform: scale(1.03);
        box-shadow: 0 0.75rem 1rem rgba(0, 0, 0, 0.25),
          0 0 0 0.2rem $focusRingColor !important;
      }
      .book-image {
        transform: translateY(-0.5rem);
        &::before {
          transform: scale(1.5);
          bottom: -1rem;
          background-color: rgba(0, 0, 0, 0.5);
        }
      }
    }
  }
  .rating {
    line-height: 1;
    margin-top: 0.5rem;
  }
}

.star {
  height: 1.5rem;
  &.half-star + .half-star {
    margin-left: -0.35rem;
  }
}

.videos,
.versions {
  section {
    max-width: 900px;
    a {
      color: inherit;
    }
    .videos-item,
    .versions-item {
      &:first-child {
        margin-top: 4rem;
      }
      div > span + span {
        margin-left: 1rem;
      }
      h2 {
        margin-bottom: 1rem;
      }
      .video-image,
      .versions-image {
        background-color: #aaa;
        border-radius: 0.2rem;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
        flex: 0.5 0 0;
        height: 10rem;
        display: block;
        box-shadow: 0 0.5rem 0.75rem rgba(0, 0, 0, 0.15);
        svg {
          width: 2.5rem;
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-45%, -50%);
          z-index: 1;
          filter: drop-shadow(0 0.2rem 0.25rem rgba(0, 0, 0, 0.25));
        }
      }
      .versions-image {
        background-position: top center;
      }
    }
  }
}
