@import "../variables";

.home-items {
  position: relative;
  a {
    color: inherit;
    text-decoration: none;
  }
  h1 {
    margin: 0;
    padding: 0;
    font: inherit;
    position: absolute;
    left: -20px;
    line-height: 1;
    top: 0;
    transform: translate(-100%, -100%) rotate(-90deg);
    transform-origin: bottom right;
    &::before {
      content: "";
      width: 0.75rem;
      height: 0.75rem;
      background-color: #aaa;
      vertical-align: middle;
      margin-right: 0.5rem;
      border-radius: 100%;
      margin-top: -0.2rem;
      display: inline-block;
    }
    a:focus {
      box-shadow: 0 0 0 0.2rem $focusRingColor !important;
    }
  }

  &.projects h1 {
    border-color: $colorProjects;
    &::before {
      background-color: $colorProjects;
    }
  }
  &.blog h1 {
    border-color: $colorBlog;
    &::before {
      background-color: $colorBlog;
    }
  }
  &.events h1 {
    border-color: $colorEvents;
    &::before {
      background-color: $colorEvents;
    }
  }
  &.travel {
    .horizontal-scroller {
      display: block;
    }
    .scroll-buttons button {
      transform: scale(0.75);
    }
    .home-highlights {
      display: flex;
      flex-wrap: nowrap;
      overflow-x: auto;
      overflow-y: visible;
      padding-top: 1rem;
    }
    h1 {
      top: 1rem;
      border-color: $colorTravel;
      &::before {
        background-color: $colorTravel;
      }
    }
    picture {
      transition: 0.3s;
    }
    article {
      margin: 0;
    }
    a:focus {
      box-shadow: none;
      picture {
        transform: scale(1.05);
        box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.25),
          0 0 0 0.2rem $focusRingColor;
      }
    }
  }

  + .home-items {
    margin-top: 4rem;
    &.travel {
      margin-top: 3rem;
    }
  }
}

.hero-container {
  max-width: 100%;
  padding: 5rem 0;
  > .container-large {
    max-width: 1100px;
    margin: 0 auto;
  }
}

.hero {
  max-width: 500px;
}

.home-items.links {
  > div > div {
    flex: 1 0 0;
  }
  h2 {
    display: block;
  }
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    li {
      margin: 0;
    }
  }
}

.home-items > div,
.projects > div {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  $gutter: 2%;
  article {
    width: (50 - ($gutter / 2));
    margin-bottom: ($gutter * 2);
    > a > span {
      display: block;
    }
  }

  h2 {
    font-size: 120%;
    margin: 0 1rem 0.5rem 0;
  }

  h2,
  time {
    display: inline;
  }

  time,
  .meta {
    opacity: 0.75;
  }

  .meta {
    margin-top: 0.25rem;
  }

  a {
    display: block;
    color: inherit;
    text-decoration: none;
  }
}

.home-items.blog .project-tags {
  margin-top: 0.75rem;
}

.middle-info {
  margin: -1.25rem auto 0;
  text-align: center;
  > div {
    height: 2.5rem;
    line-height: 2.45rem;
    padding: 0 1.5rem;
    border-radius: 2rem;
    display: inline-block;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 85%;
    word-spacing: 0.25rem;
    background-color: #fff;
    box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1);
  }
}
